:root {
  --primary-color: #46a7bb;
  --secondary-color: #9d4edd;
  --accent-color: #ff006e;
  --success-color: #06ffa5;
  --warning-color: #ff9e00;
  --bg-dark: #0a0e27;
  --bg-darker: #050814;
  --glass-bg: rgba(255, 255, 255, 0.05);
  --glass-bg-input: rgb(255, 255, 255);
  --glass-border: rgba(255, 255, 255, 0.1);
  --text-primary: #ffffff;
  --text-secondary: #b8c1ec;
  --card-bg: rgba(13, 27, 42, 0.85);
  --card-border: rgba(70, 167, 187, 0.4);
  --cyan-glow: #00d4ff;
  --pink-gradient: linear-gradient(135deg, #ff006e 0%, #9d4edd 100%);
  --teal-solid: #00b4d8;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: var(--font-space-mono), 'Space Mono', monospace;
  background-image: url(/assets/bg3.png);
  background-size: cover;
  color: var(--text-primary);
  overflow-x: hidden;
  min-height: 100vh;
  position: relative;
}
body::before {
  content: "";
  position: fixed;
  inset: 0;
  background: linear-gradient(135deg, rgba(10, 14, 39, 0.808) 0%, rgb(26, 16, 66) 50%, rgba(10, 14, 39, 0.7) 100%);
  animation: gradientShift 15s ease infinite;
}
@keyframes gradientShift {
  0%,
  100% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.5;
  }
}
.custom-cursor {
  position: fixed;
  width: 25px;
  height: 25px;
  pointer-events: none;
  z-index: 10000;
  transform: translate(-50%, -50%);
  transition: transform 0.05s ease;
}
.cursor-asteroid {
  width: 100%;
  height: 100%;
  background: url(/assets/meteor.png) center/contain no-repeat;
  border-radius: 50%;
  animation: asteroidRotate 3s linear infinite;
}
@keyframes asteroidRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#screen-minigame.active {
  cursor: auto !important;
}
#screen-minigame.active .custom-cursor {
  display: none;
}
.falling-asteroids {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
  overflow: hidden;
}
.asteroid {
  position: absolute;
  background: url(/assets/meteor.png) center/contain no-repeat;
  border-radius: 50%;
  animation: fall linear infinite;
  opacity: 0.6;
  box-shadow: 0 0 5px rgba(139, 69, 19, 0.5);
}
@keyframes fall {
  0% {
    transform: translateY(-100px) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 0.6;
  }
  90% {
    opacity: 0.6;
  }
  100% {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(5, 8, 20, 0.95);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.loading-overlay.hidden {
  opacity: 0;
  pointer-events: none;
}
.loading-gif {
  width: 500px;
  margin-bottom: 1rem;
  border-radius: 10px;
}
.loading-spinner {
  width: 60px;
  height: 60px;
  border: 4px solid rgba(0, 212, 255, 0.1);
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-text {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  color: var(--primary-color);
  font-size: 1.2rem;
  animation: pulse 1.5s ease-in-out infinite;
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
.countdown-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(5, 8, 20, 0.9);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.3s ease;
}
.countdown-number {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 15rem;
  font-weight: 900;
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color), var(--accent-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 10px rgba(0, 213, 255, 0.582);
  animation: countdownPulse 1s ease;
  filter: drop-shadow(0 0 10px rgba(0, 212, 255, 0.6));
}
@keyframes countdownPulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.screen {
  display: none;
  height: 100vh;
  height: 100dvh;
  padding: 3rem 2rem;
  position: relative;
  z-index: 1;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.screen.active {
  display: flex;
  animation: fadeIn 0.5s ease-in-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.container {
  width: 100%;
  max-width: 900px;
  margin: 500px auto;
}
.glass-panel {
  background: var(--glass-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--glass-border);
  border-radius: 20px;
  padding: 1.5rem 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  animation: glassAppear 0.6s ease-out;
}
.glass-panel.wide {
  max-width: 1200px;
}
@keyframes glassAppear {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.neon-title {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 3.5rem;
  font-weight: 900;
  text-align: center;
  margin-bottom: 1rem;
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
  animation: neonPulse 2s ease-in-out infinite;
}
@keyframes neonPulse {
  0%,
  100% {
    filter: brightness(1);
  }
  50% {
    filter: brightness(1.2);
  }
}
.section-title {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 0.75rem;
  color: var(--primary-color);
}
.subtitle {
  text-align: center;
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}
.pilot-name-display {
  text-align: center;
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: var(--success-color);
}
.pilot-name-display span {
  font-weight: 700;
  color: var(--primary-color);
}
.form-group {
  margin-bottom: 1rem;
}
.form-group label {
  display: block;
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
  color: var(--primary-color);
  text-transform: uppercase;
  letter-spacing: 2px;
}
input[type="text"] {
  width: 100%;
  padding: 1rem 1.5rem;
  font-family: var(--font-space-mono), 'Space Mono', monospace;
  font-size: 1.1rem;
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 10px;
  color: var(--text-primary);
  transition: all 0.3s ease;
}
input[type="text"]:focus {
  outline: none;
  border-color: var(--primary-color);
}
.btn-primary {
  position: relative;
  width: 100%;
  padding: 1rem 1.5rem;
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
  border: none;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease;
  margin-top: 1rem;
}
.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.4);
}
.btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.btn-primary span {
  position: relative;
  z-index: 2;
}
.btn-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}
.btn-primary:hover:not(:disabled) .btn-glow {
  width: 300px;
  height: 300px;
}
.option-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}
.option-btn {
  padding: 0.75rem 0.5rem;
  background: rgba(255, 255, 255, 0.637);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 12px;
  color: var(--bg-dark);
  font-family: var(--font-space-mono), 'Space Mono', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
  backdrop-filter: blur(5px);
}
.option-btn:hover {
  background: rgba(255, 255, 255, 1);
  border-color: var(--primary-color);
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.4);
}
.option-btn.selected {
  background: rgba(255, 255, 255, 1);
  border-color: var(--primary-color);
  box-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
}
.option-number {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-color);
}
.option-label {
  font-size: 0.75rem;
  color: var(--bg-dark);
}
.option-label-lvl {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--primary-color);
  text-transform: uppercase;
}
.spaceship-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.75rem;
  margin-bottom: 1rem;
}
.spaceship-card {
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.63);
  border: 2px solid rgba(0, 212, 255, 0.3);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(5px);
}
.spaceship-card:hover {
  transform: translateY(-5px) scale(1.02);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.4);
  background: rgba(255, 255, 255, 1);
  border-color: var(--primary-color);
}
.spaceship-card.selected {
  border-color: var(--primary-color);
  background: rgba(255, 255, 255, 1);
  box-shadow: 0 0 30px rgba(0, 212, 255, 0.5);
}
.spaceship-image {
  width: 80px;
  height: 55px;
  object-fit: contain;
  margin: 0 auto 0.5rem;
  display: block;
  filter: drop-shadow(0 0 10px currentColor);
  animation: float 3s ease-in-out infinite;
  transition: transform 0.3s ease;
}
.spaceship-card:hover .spaceship-image {
  transform: translateY(-10px) scale(1.1);
  animation: none;
}
.spaceship-emoji {
  font-size: 3rem;
  margin-bottom: 0.5rem;
  display: block;
  animation: float 3s ease-in-out infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.spaceship-name {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.85rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}
.spaceship-desc {
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}
.spaceship-speed {
  font-size: 0.9rem;
  color: var(--success-color);
}
.quiz-header {
  margin-bottom: 2rem;
}
.quiz-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}
.quiz-counter {
  color: var(--primary-color);
}
.quiz-score {
  color: var(--success-color);
  font-weight: 700;
}
.progress-bar {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  overflow: hidden;
}
.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--secondary-color));
  transition: width 0.3s ease;
  border-radius: 10px;
}
.question-container {
  margin-top: 2rem;
}
.question-text {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 2rem;
  line-height: 1.6;
  text-align: center;
  color: var(--text-primary);
}
.answers-grid {
  display: grid;
  gap: 1rem;
}
.answer-btn {
  padding: 1.2rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid var(--glass-border);
  border-radius: 10px;
  color: var(--text-primary);
  font-family: var(--font-space-mono), 'Space Mono', monospace;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
}
.answer-btn:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--primary-color);
  transform: translateX(5px);
}
.answer-btn.correct {
  background: rgba(6, 255, 165, 0.2);
  border-color: var(--success-color);
  animation: correctPulse 0.5s ease;
}
.answer-btn.incorrect {
  background: rgba(255, 0, 110, 0.2);
  border-color: var(--accent-color);
  animation: shake 0.5s ease;
}
.answer-btn:disabled {
  cursor: not-allowed;
}
@keyframes correctPulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}
@keyframes shake {
  0%,
  100% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-10px);
  }
  75% {
    transform: translateX(10px);
  }
}
#screen-minigame {
  height: 100vh;
  height: 100dvh;
  max-height: 100vh;
  max-height: 100dvh;
  padding: 0;
  margin: 0;
  overflow: hidden;
  cursor: none;
}
#screen-minigame.game-fullscreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100;
}
#minigame-canvas {
  display: block;
  width: 100vw;
  height: 100vh;
  position: absolute;
