  align-items: center;
  justify-content: center;
  margin-bottom: 0.3rem;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  animation: iconFloat 3s ease-in-out infinite;
}
@keyframes iconFloat {
  0%,
