  color: var(--text-primary);
  letter-spacing: 2px;
}
.status-subtext {
  font-size: 0.65rem;
  color: var(--text-secondary);
  letter-spacing: 1px;
}
.waiting-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.section-left {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}
.section-icon {
  font-size: 1.2rem;
}
.section-title {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  letter-spacing: 2px;
}
.section-right {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.capacity-display {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}
.capacity-current {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 1rem;
  font-weight: 700;
  color: var(--text-primary);
}
.capacity-separator {
  color: var(--text-secondary);
}
.capacity-max {
  font-size: 0.9rem;
  color: var(--text-secondary);
}
.capacity-bar-mini {
  width: 80px;
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  overflow: hidden;
  margin-left: 0.5rem;
}
.capacity-fill-mini {
  height: 100%;
  background: linear-gradient(90deg, var(--primary-color), var(--success-color));
  border-radius: 10px;
}
.waiting-player-grid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  gap: 0.75rem;
  margin-bottom: 2rem;
}
.waiting-player-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1rem 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  position: relative;
  transition: all 0.3s ease;
}
.waiting-player-card:hover:not(.empty) {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--primary-color);
}
.waiting-player-card.current-user {
  border-color: var(--primary-color);
  background: rgba(70, 167, 187, 0.1);
  box-shadow: 0 0 20px rgba(70, 167, 187, 0.2);
}
.waiting-player-card.empty {
  opacity: 0.3;
}
.you-badge {
  position: absolute;
  top: -8px;
  right: -8px;
  background: var(--primary-color);
  color: white;
  font-size: 0.55rem;
  font-weight: 700;
  padding: 0.2rem 0.4rem;
  border-radius: 4px;
  letter-spacing: 1px;
}
.player-spacecraft-icon {
  width: 50px;
  height: 40px;
  object-fit: contain;
}
.refit-section {
  display: flex;
  justify-content: center;
  margin-top: auto;
  margin-bottom: 2rem;
}
.btn-refit {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 2rem;
  background: rgba(0, 50, 60, 0.6);
  border: 2px solid var(--primary-color);
  border-radius: 12px;
  color: var(--primary-color);
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.9rem;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn-refit:hover {
  background: rgba(70, 167, 187, 0.2);
  box-shadow: 0 0 30px rgba(70, 167, 187, 0.3);
  transform: translateY(-2px);
}
.refit-icon {
  font-size: 1.2rem;
}
.dialog-overlay {
  position: fixed;
  inset: 0;
  background: rgba(5, 8, 20, 0.9);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: fadeIn 0.3s ease;
}
.dialog-content {
  background: linear-gradient(135deg, rgba(20, 30, 50, 0.95) 0%, rgba(10, 15, 30, 0.98) 100%);
  border: 1px solid var(--glass-border);
  border-radius: 20px;
  padding: 2rem;
  position: relative;
  max-width: 90vw;
  max-height: 90vh;
  overflow-y: auto;
  animation: scaleIn 0.3s ease;
}
@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}
.dialog-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 36px;
  height: 36px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--glass-border);
  border-radius: 50%;
  color: var(--text-secondary);
  font-size: 1.2rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.dialog-close:hover {
  background: rgba(255, 59, 48, 0.2);
  border-color: #ff3b30;
  color: #ff3b30;
}
.dialog-title {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 0.5rem;
  letter-spacing: 3px;
}
.dialog-subtitle {
  font-size: 0.9rem;
  color: var(--text-secondary);
  text-align: center;
  margin-bottom: 1.5rem;
}
.qr-dialog {
  width: 400px;
}
.qr-code-large {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}
.game-code-display-large {
  text-align: center;
  margin-bottom: 1.5rem;
}
.code-label {
  display: block;
  font-size: 0.7rem;
  color: var(--text-secondary);
  letter-spacing: 2px;
  margin-bottom: 0.5rem;
}
.code-value {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 2rem;
  font-weight: 900;
  color: var(--success-color);
  letter-spacing: 6px;
}
.dialog-actions {
  display: flex;
  justify-content: center;
  width: 100%;
}
.url-card-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 0 1rem;
}
.url-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  width: 100%;
  max-width: 100%;
  padding: 1.2rem 1.5rem;
  background: linear-gradient(135deg, rgba(70, 167, 187, 0.15) 0%, rgba(157, 78, 221, 0.1) 100%);
  border: 1.5px solid rgba(70, 167, 187, 0.5);
  border-radius: 12px;
  color: var(--text-secondary);
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
}
.url-text {
  flex: 1;
  font-size: 0.9rem;
  word-break: break-all;
  font-family: 'Courier New', monospace;
  letter-spacing: 0.5px;
  color: var(--text-primary);
  font-weight: 500;
}
.url-copy-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  padding: 0;
  background: rgba(255, 255, 255, 0.08);
  border: 1.5px solid rgba(70, 167, 187, 0.4);
  border-radius: 10px;
  color: var(--primary-color);
  cursor: pointer;
  transition: all 0.3s ease;
  flex-shrink: 0;
}
.url-copy-btn:hover {
  background: rgba(70, 167, 187, 0.25);
  border-color: var(--primary-color);
  transform: scale(1.08);
  box-shadow: 0 0 15px rgba(70, 167, 187, 0.3);
}
.url-copy-btn.success {
  background: rgba(6, 255, 165, 0.25);
  border-color: var(--success-color);
  color: var(--success-color);
  box-shadow: 0 0 15px rgba(6, 255, 165, 0.3);
}
.btn-copy-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, var(--primary-color) 0%, var(--teal-solid) 100%);
  border: none;
  border-radius: 10px;
  color: white;
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn-copy-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 20px rgba(70, 167, 187, 0.4);
}
.rocket-dialog {
  width: 700px;
}
.rocket-select-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.rocket-select-card {
  background: var(--glass-bg);
  border: 2px solid var(--glass-border);
  border-radius: 12px;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}
.rocket-select-card:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--primary-color);
  transform: translateY(-3px);
}
.rocket-select-card.selected {
  border-color: var(--success-color);
  background: rgba(6, 255, 165, 0.1);
  box-shadow: 0 0 20px rgba(6, 255, 165, 0.2);
}
.rocket-image-container {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.75rem;
}
.rocket-image {
  max-width: 100%;
  max-height: 70px;
  object-fit: contain;
}
.rocket-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.25rem;
}
.rocket-name {
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.85rem;
  font-weight: 600;
}
.rocket-desc {
  font-size: 0.7rem;
  color: var(--text-secondary);
  text-align: center;
}
.current-badge {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background: var(--success-color);
  color: white;
  font-size: 0.55rem;
  font-weight: 700;
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  letter-spacing: 1px;
}
.btn-dialog-cancel {
  width: 100%;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--glass-border);
  border-radius: 10px;
  color: var(--text-secondary);
  font-family: var(--font-orbitron), 'Orbitron', sans-serif;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn-dialog-cancel:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: var(--primary-color);
  color: var(--text-primary);
}
@media (max-width: 1200px) {
  .host-lobby-content {
    grid-template-columns: 1fr;
  }
  .host-right-panel {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
  }
  .game-code-section,
  .qr-section {
    flex: 1;
    min-width: 250px;
  }
  .status-cards {
    width: 100%;
    flex-direction: row;
  }
  .player-grid {
    grid-template-columns: repeat(4, 1fr);
  }
  .waiting-player-grid {
    grid-template-columns: repeat(4, 1fr);
  }
  .podium-container {
    flex-wrap: wrap;
  }
}
@media (max-width: 768px) {
  .host-header,
  .waiting-header,
  .monitor-header,
  .leaderboard-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  .host-actions {
    width: 100%;
    justify-content: space-between;
  }
  .player-grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .waiting-player-grid {
    grid-template-columns: repeat(3, 1fr);
  }
  .rankings-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .rocket-select-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .podium-container {
    flex-direction: column;
    align-items: center;
  }
  .podium-card.center {
    order: -1;
    transform: none;
  }
  .game-code {
    font-size: 2rem;
    letter-spacing: 5px;
  }
}
